<template>
  <div class="cafe__details">
    <div class="details">
      <h2 class="details__title"> {{ cafe.name }}</h2>
      <p class="details__subtitle"><strong>Кухня:</strong> {{ cafe.cuisine ? cafe.cuisine : 'Информация отсутсвует' }}</p>
      <p class="details__subtitle"><strong>Бизнес ланч:</strong> {{ cafe.business_lunch ? cafe.business_lunch : 'Информация отсутсвует' }}</p>
      <p class="details__subtitle"><strong>Средний чек:</strong> {{ cafe.price ? cafe.price + ' руб' : 'Информация отсутсвует' }}</p>
      <p class="details__subtitle"><strong>Адрес:</strong> {{ cafe.address ? cafe.address : 'Информация отсутсвует' }}</p>
      <p class="details__subtitle"><strong>Как добраться:</strong> {{ cafe.landmark ? cafe.landmark : 'Информация отсутсвует' }}</p>
      <p class="details__subtitle"><strong>Расстояние:</strong> {{ cafe.distance ? cafe.distance + ' метров' : 'Информация отсутсвует' }}</p>
      <button class="cafe__btn" @click="shareCafe">Поделиться</button>
    </div>
    <div>
      <img class="cafe__img" :src="cafe.photo" :alt="cafe.name">
    </div>
    <div class="copy-text" v-if="showCopy">{{ copyText }}</div>
  </div>
</template>

<script>

export default {
  props: ['cafe'],
  data() {
    return {
      showCopy: false,
      copyText: '',
    }
  },
  methods: {
    shareCafe() {
      const copyDetailsInfo = `
      Название: ${this.cafe.name}
      Кухня: ${this.cafe.cuisine}
      Адрес: ${this.cafe.address}
      Как добраться: ${this.cafe.landmark}
      `;

      navigator.clipboard.writeText(copyDetailsInfo)
      .then(() => {
        this.copyText = 'Скопировано';
        this.showCopy = true;
        setTimeout(() => {
          this.showCopy = false;
        }, 2000);
      })
      .catch(error => {
        console.log('Не удалось скопировать данные', error);
      })
    },
  },
};
</script>

