<template>
  <div class="container">
    <div class="cafe">
      <h1 class="cafe__title">Где будем кушать?</h1>
      <h2 class="cafe__subtitle"> Список заведений </h2>
      <button class="cafe__btn" @click="selectRandomCafe">Выбрать случайное кафе</button>
      <cafeList v-if="cafes.length > 0" :cafes="cafes" />
      <cafeDetails v-if="selectedCafe" :cafe="selectedCafe" />
    </div>
  </div>
</template>

<script>

import axios from 'axios';
import cafeList from "./components/cafeList.vue";
import cafeDetails from "./components/cafeDetails.vue";

export default {
  components: {
    cafeList,
    cafeDetails,
  },
  data() {
    return {
      cafes: [],
      selectedCafe: null,
    };
  },
  mounted() {
    this.fetchCafes();
    console.log(this.cafes);
  },
  methods: {
    fetchCafes() {
      axios.get('https://bandaumnikov.ru/api/test/site/get-index')
        .then(res => {
          this.cafes = res.data.data;
          console.log('res.data: ', res.data.data);
        })
        .catch(err => {
          console.log(err);
        });
    },
    selectRandomCafe() {
      const randomIndex = Math.floor(Math.random() * this.cafes.length);
      this.selectedCafe = this.cafes[randomIndex];
    },
  },
}
</script>

